/* Runtime-controlled scrollbar compensation
   (JS sets --sbw and toggles .has-scroll on <html>) */
:root {
  --sbw: 0px; /* scrollbar width (computed at runtime) */
}
html.no-scroll body {
  /* When overflow occurs, add padding equal to the scrollbar width
     so the content area width stays constant and doesn't “jump”. */
  padding-right: var(--sbw);
}
html.no-scroll .header,
html.no-scroll .footer { padding-right: var(--sbw); 
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
padding-right: calc(100vw - 100%);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* Prevent bg scroll when the header menu is open */
html.menu-open, html.menu-open body {
  overflow: hidden;
}
/* Always reserve scrollbar space (prevents jump) but hide the track */
html { overflow-y: scroll; }
html::-webkit-scrollbar { width: 0; height: 0; } /* Chrome/Safari/Edge */
html { scrollbar-width: none; }                   /* Firefox */